import { handleActions } from 'redux-actions';
import { AUTHORIZATION, FETCH_TASKS, SET_USER_INFO, LOADING } from './../constants';
import { uniqBy } from 'lodash';

export const authorization = handleActions({
    [AUTHORIZATION]: (state, action) => {
        const { token, auth } = action.payload;
        return { token, auth }
    }
}, {
    token: '',
    auth: false
});

export const tasks = handleActions({
    [FETCH_TASKS]: (state, action) => {
        return uniqBy(state.concat(action.payload) , 'oid')
    }
}, []);

export const user_info = handleActions({
    [SET_USER_INFO]: (state, action) => action.payload
}, {});

export const loading = handleActions({
    [LOADING]: (state, action) => action.payload
}, false)