import { SET_USER_INFO } from './../constants/';
import { PROXY, API_URL, GET_USER } from '../constants/api';

export const fetchUserInfo = oid => {
    return dispatch => {
        fetch(`${PROXY}${API_URL}${GET_USER}/${oid}`)
            .then(response => response.json())
            .then(user_info => dispatch({ type: SET_USER_INFO, payload: user_info }))
            .catch( error => console.log( error ));

    }
}