import React, { Fragment } from 'react';
import { Button } from 'antd';
import AppIcon from '../layouts/components/AppIcon';

const toolbar_wrapper_style = {
    position: 'absolute',
    bottom: 0,
    width: '100%'
}

const TaskToolBar = ({ taskID }) => {
    return (
        <Fragment>
            <div style={toolbar_wrapper_style} className="d-flex justify-content-around">
                <Button icon={<AppIcon i="fa-camera-retro" t="fad" />} size="large" />
                <Button icon={<AppIcon i="fa-camera-retro" t="fad" />} size="large" />
                <Button icon={<AppIcon i="fa-camera-retro" t="fad" />} size="large" />
            </div>
        </Fragment>
    )
}

export default TaskToolBar;