import { PROXY, API_URL, LOGIN_ENDPOINT, GET_ALL_USERS } from './../constants/api';
import { AUTHORIZATION, APP_COOKIE_NAME } from '../constants';
import { setCookie } from '../utils';
import { find } from 'lodash';

export const login = credentials => {
    return dispatch => {

        // USER OID
        let oid = '';

        //GET ALL USERS AND CHECK USERNAME
        fetch(PROXY + API_URL + GET_ALL_USERS, { method: 'GET' })
            .then(response => response.json())
            .then(all_users => {
                console.log("all_users", all_users)
                const current_user = find(all_users, ['userName', credentials.username]);
                if (current_user.isActive) {
                    oid = current_user.oid;

                    //CHECK USERNAME MATCH PASSWORD
                    fetch(`${PROXY}${API_URL}${LOGIN_ENDPOINT}${oid}/${credentials.password}`)
                        .then(response => response.json())
                        .then(isUserAuthorized => {
                            if (isUserAuthorized) {
                                dispatch({
                                    type: AUTHORIZATION,
                                    payload: { token: oid, auth: true }
                                });
                                setCookie(APP_COOKIE_NAME, oid, 1); //TOKEN VALIDO UN DIA
                            }
                        }
                    )
                }
            }
        )
    }
}