import React, { Component } from 'react';
import { connect } from 'react-redux';
import BasicLayout from '../layouts/BasicLayout';
import { Route, withRouter } from 'react-router-dom';

// PAGINAS
import Home from '../pages/Home';
import Tasks from '../pages/Tasks';
import TaskExtended from '../pages/TaskExntended';
import AppMenu from '../layouts/components/AppMenu';

// TASK FETCH
import { fetchTasks } from './../actions/tasksActions';

// USER DATA FETCH
import { fetchUserInfo } from './../actions/fetchUserInfo';

class Main extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selected: this.props.location.pathname.replace('/tasks/', '').split('/')[0]
        }
    }
    componentDidMount() {
        this.props.fetchUserInfo(this.props.token);
        if (this.props.location.pathname !== '/' || this.state.selected) {
            this.props.fetchTasks(this.state.selected, this.props.token)
        }
    }

    handleSelected = selected => {
        if (selected !== '/') {
            this.props.fetchTasks(selected.replace('/tasks/', ''), this.props.token)
        }
    }

    render() {
        return (
            <BasicLayout>
                <Route exact path="/" component={props => <Home />} />
                <Route exact path="/tasks/:type"
                    component={props => <Tasks data={this.props.tasks} type={props.match.params.type} />} />
                <Route exact path="/tasks/:type/:id"
                    component={props => {
                        const data = this.props.tasks.find( r => r.oid === Number(props.match.params.id) )
                        return <TaskExtended data={data} />
                    }
                    } />
                <AppMenu handleSelected={this.handleSelected} />
            </BasicLayout>
        );
    }
}

Main.defaultProps = {
    token: '',
    tasks: [],
}

const mapStateToProps = (state, props) => (
    {
        token: state.authorization.token,
        tasks: state.tasks
    });
const mapDispatchToProps = ({ fetchTasks, fetchUserInfo });
const MainConnected = connect(mapStateToProps, mapDispatchToProps)(Main);
export default withRouter(MainConnected)