import React, { Fragment } from 'react';
import { ReactSVG } from 'react-svg';
import { Button } from 'antd';

const levels = [
    { icon: "chevron-double-down", color: "#ff874d", label: "Baja" },
    { icon: "grip-lines", color: "#52c41a", label: "Media" },
    { icon: "chevron-double-up", color: "#d9363e", label: "Alta" },
]



const PriorityIcon = ({ level, className }) => {
    const statusIcon = require(`!!file-loader!./../assets/svgs/duotone/${level || level === 0 ? levels[level].icon : 'exclamation-triangle'}.svg`);
    return (
        <Fragment>
            {level || level === 0
                ? <div className={className ? className : ''}>
                    <Button shape="circle" type="dashed" style={{}} className="app-status-icon"
                        icon={<ReactSVG src={statusIcon} style={{ fill: levels[level].color }} />} />
                </div>
                : <span>404 icon.</span>}
        </Fragment>
    );
};

export default PriorityIcon;